import common from '../../common/common.js'

export default {
	data() {
		return {
			baseUrl:common.baseUrl,
			userid:'',
			orderList:[],
			type:0,				//类别，0:买菜，1:拼团
			page:0,
			isall:0,
			info:{
				'bg_color':'255,255,255',
				'bg_alpha':1,
				delta:2,
			},
			
			titleHeight:0,
			statusBarHeight:0,
			naviBarHeight:0,
			superTitleHeight:0,
		};
	},
	onLoad(e) {
		var that = this
		var userid = uni.getStorageSync('userid');
		that.userid = userid
		
		that.getDarenSuperOrders();			 
		
		
		that.titleHeight = common.titleHeight;
		that.statusBarHeight = common.statusBarHeight;
		that.naviBarHeight = common.naviBarHeight;
		that.superTitleHeight = common.titleHeight + 50;



		
	},
	onPullDownRefresh() {
		var that = this
		uni.showNavigationBarLoading();
		console.log('下拉刷新中...');	

		that.resetAttr();
		that.getDarenSuperOrders();
		
		uni.hideNavigationBarLoading();
		uni.stopPullDownRefresh();
	},
	methods:{		
		
		//重置参数
		resetAttr(){
			var that = this
			that.page = 0;
			that.isall = 0;
			that.orderList = []
		},
		
		
		//获取达人推广订单列表
		getDarenSuperOrders(){
			var that = this
			var userid = uni.getStorageSync('userid');
			var isall = that.isall
			console.log('isall',isall)
			if(isall == 1){
				return false;
			}
			uni.request({
				url: common.apiUrl+'orders/getDarenSuperOrders',
				method: 'GET',
				data: {
					userid:userid,
					page:that.page+1
				},
				success: res => {
					console.log(res);
					that.orderList = that.orderList.concat(res.data.order_list);
					that.isall = res.data.isall;
					that.page = res.data.page
				},
				fail: () => {},
				complete: () => {}
			});
		},
		
		//加载更多
		loadmore(e){
			var that = this
			var loadtype = e.currentTarget.dataset.loadtype
			that.getUserOrders();
		},
		
		
	}
}
